@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: "Lato", "Helvetica Neue", sans-serif;
  background-color: #f4f4f7;
}

h1 {
  font-family: "Anton", sans-serif;
}

a.link-unstyled {
  color: inherit;
}

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none;
}

.btn {
  padding: 0.375rem 2rem;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #7dcd40;
  border-color: #7dcd40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #7dcd40;
  border-color: #7dcd40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
  color: #fff;
  background-color: #cd4040;
  border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #cd4040;
  border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #cd4040;
  border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}

.contenedorPrincipal {
  height: 625px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.contenedorPrincipalListado,
.contenedorPrincipal {
  background-image: url(data:image/jpeg;base64,);
}

.botonPagina {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 1rem;
}

.imagenInicio {
  width: 300px;
  height: 220px;
  margin: -70px 0px 6px 21px;
}

/* pagina no existente */
.contNotFound {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  height: 600px;
}

.imagenNotFound {
  width: 300px;
  height: 300px;
}

/* pagina listado */

.listContenedor {
  padding-top: 15px;
}
.botonListPagina {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-top: 1rem;
  margin-right: 1rem;
}

.List ul > li {
  margin-bottom: 1rem;
}

.List ul {
  width: 50%;
}

.List {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  margin: 0px auto 0px auto;
}

.ListItem {
  background: #ffffff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.23);
  border-radius: 5px;
  display: -webkit-flex;
  display: flex;
  padding: 1rem;
}

.ListItem__avatar {
  border-radius: 50%;
  background: #a3a3a3;
  margin-right: 0.5rem;
  width: 80px;
  height: 80px;
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -moz-crisp-edges;
      image-rendering: pixelated;
}

.datos {
  text-align: justify;
  margin-left: 5px;
}

.badge {
  background: #ffffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5);
  border-radius: 8px 8px 8px 8px;
  overflow: hidden;
  width: 670px;
  max-width: 100%;
  padding: 0 !important;
}
.badge_header {
  padding: 0.5rem 0;
  height: 38px;
  background: rgb(31 154 145);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  font-size: 20px;
  color: #1b1b25;
}

.badge_section-name {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem 0;
}

.badge_avatarPrin {
  border-radius: 50%;
  margin-right: 1rem;
  width: 115px;
  height: 115px;
}

.badge_footer {
  height: 54px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-weight: bold;
  font-style: italic;
  background: #f4f4f7;
}

.datosRow {
  -webkit-justify-content: center;
          justify-content: center;
}
.datosMascota {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.formato {
  text-align: justify;
  width: 130px;
  margin-left: 31px;
  font-size: 12px;
  padding-bottom: 9px;
}

.form-control-inputs {
  display: block;
  width: 70%;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.inicio {
  padding-left: 15px;
  text-decoration: none;
}
.navTitulo {
  font-weight: 400;
  color: beige;
  padding-left: 15px;
}

.navContenedor {
  width: 100%;
  background-color: rgb(31 154 145);
  color: #ffffff;
}

.imgNav {
  width: 30px;
  height: 30px;
}

